.app_container {
  margin-left: 5px;
  margin-right: 5px;
  max-width: 400px;
  margin: auto;
}

.header_links {
  text-align: center;
}

.app_header {
  text-align: center;
}

.app_rule {
  border: 0;
  clear: both;
  display: block;
  width: 96%;
  background-color: #000;
  height: 1px;
  margin-bottom: 10px;
  margin-top: 10px;
}

.app_footer {
  text-align: center;
  color: #888;
}

.app_footer a {
  color: #888;
}