.builder_container {
    display: flex;
    flex-direction: column;
}

.bottom_buttons {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
}

.file_picker {
    margin-top: 5px;
    margin-bottom: 5px;
}

.no_fields {
    color: #555;
    text-align: center;
}

.table_fields {
    margin-top: 10px;
}

td:first-child {
    width:100%;
}

thead th {
    border-bottom: 1px solid black;
    border-collapse: collapse;
}

th, td {
    padding-top: 3px;
    padding-bottom: 3px;
}

table {
    border-collapse: collapse;
}

.name_cell {
    padding-right: 10px;
    width: 100%;
}

.row_center {
    text-align: center;
}

.entry_form {
    display: flex;
}

.entry_name {
    width: 100%;
}